import React, { Component } from 'react';
import request from 'superagent';

class Index extends Component {

    constructor(props) {
        super(props);
        this.state = {
          photos: []
        }
    }

    componentWillMount() {
        this.fetchPhotos();
    }

    fetchPhotos() {
        request
            .get('https://api.instagram.com/v1/users/self/?access_token=3437315836.709c521.fb10cbf083df485b85c08d5b9ecc86fb')
            .then((res) => {
            this.setState({
                photos: res.body.data
            })
        })
    }

    render() {
        return (
            <div>
                {console.log(this.state.photos)}
            </div>
        );
    }


}

export default Index


